import { ClientType, Person } from 'generated/prisma';
import { ClientForm } from '../forms/client.forms';
import { validatePersonForm } from './person.validators';
import { CompanyFormErrors, validateCompanyForm } from './company.validators';
import { FormErrors } from '../errors/validation.error';

export const validateClientForm = (form: ClientForm): FormErrors<Person> | CompanyFormErrors => {
  if (form.type === ClientType.PERSON) {
    return validatePersonForm(form.data);
  } else {
    return validateCompanyForm(form.data);
  }
};
